import React, { Component } from 'react';
import {
    Link,
    withRouter
} from 'react-router-dom';
import './AppHeader.css';
import pollIcon from '../poll.svg';
import { Layout, Menu, Dropdown, Icon } from 'antd';
import { loadVersionNumber} from '../util/APIUtils';
import { getAvatarColor } from '../util/Colors';

const Header = Layout.Header;
    
class AppHeader extends Component {
    constructor(props) {
        super(props);   
        this.handleMenuClick = this.handleMenuClick.bind(this); 
		this.state = {
			versionNumber: "0.0.0",
			isLoading: false
		};
		this.loadVersion = this.loadVersion.bind(this);
		
    }
	
	loadVersion() {
		
		this.setState({
		  versionNumber: "0.0.0",
		  isLoading: true
		});
		loadVersionNumber()
		.then(response => {
		   //console.log(response);
		  this.setState({
			versionNumber: response.version,
			isLoading: false
		  });
		  
		}).catch(error => {
		  this.setState({
			versionNumber: "0.0.0",
			isLoading: false
		  });
		  //console.log("err");
		});
	}
	  
	  
    handleMenuClick({ key }) {
      if(key === "logout") {
        this.props.onLogout();
      }
    }
	
	componentDidMount() {
        this.loadVersion();
    }

	
    render() {
        let menuItems;
			
        if(this.props.currentUser) {
          menuItems = [
            <Menu.Item key="/techweek-polls">
              <Link to="/techweek-polls">
                <Icon type="home" className="nav-icon" />
              </Link>
            </Menu.Item>,
            <Menu.Item key="/techweek-polls/poll/new">
            <Link to="/techweek-polls/poll/new">
              <img src={pollIcon} alt="poll" className="poll-icon" />
            </Link>
          </Menu.Item>,
          <Menu.Item key="/techweek-polls/profile" className="profile-menu">
                <ProfileDropdownMenu 
                  currentUser={this.props.currentUser} 
                  handleMenuClick={this.handleMenuClick}/>
            </Menu.Item>
          ]; 
        } else {
		  
          menuItems = [
            <Menu.Item key="/techweek-polls/login">
              <Link to="/techweek-polls/login">Login</Link>
            </Menu.Item>,
            <Menu.Item key="/techweek-polls/signup">
              <Link to="/techweek-polls/signup">Signup</Link>
            </Menu.Item>                  
          ];
        }

        return (
            <Header className="app-header">
            
			<div className="app-version"
                                style={{ color: getAvatarColor(this.state.versionNumber)}} >
			[Version: {this.state.versionNumber}]  
              </div>
			  <div className="container">
              <div className="app-title" >  
	<Link to="/techweek-polls">Polling App for Tech Week DEMO (Advance Project BSC)</Link>
              </div>
              <Menu
                className="app-menu"
                mode="horizontal"
                selectedKeys={[this.props.location.pathname]}
                style={{ lineHeight: '64px' }} >
                  {menuItems}
              </Menu>
            </div>
          </Header>
        );
    }
}

function ProfileDropdownMenu(props) {
  const dropdownMenu = (
    <Menu onClick={props.handleMenuClick} className="profile-dropdown-menu">
      <Menu.Item key="user-info" className="dropdown-item" disabled>
        <div className="user-full-name-info">
          {props.currentUser.name}
        </div>
        <div className="username-info">
          @{props.currentUser.username}
        </div>
      </Menu.Item>
      <Menu.Divider />
      <Menu.Item key="profile" className="dropdown-item">
        <Link to={`/techweek-polls/users/${props.currentUser.username}`}>Profile</Link>
      </Menu.Item>
      <Menu.Item key="logout" className="dropdown-item">
        Logout
      </Menu.Item>
    </Menu>
  );

  return (
    <Dropdown 
      overlay={dropdownMenu} 
      trigger={['click']}
      getPopupContainer = { () => document.getElementsByClassName('profile-menu')[0]}>
      <a className="ant-dropdown-link">
         <Icon type="user" className="nav-icon" style={{marginRight: 0}} /> <Icon type="down" />
      </a>
    </Dropdown>
  );
}


export default withRouter(AppHeader);


